import type P5 from 'p5';

export function HelperFunctions(p: P5) {
    //p5.dom click click events. Notice that there is no this. at the
    //start we don't need to do that here because the event will
    //be added to the button and doesn't 'belong' to the object

    //event handler for the clear button event. Clears the screen
    p.select('#clear-btn').mouseClicked(function () {
        p.background(255);

        //call loadPixels to update the drawing state
        //this is needed for the mirror tool
        p.loadPixels();
    });

    //event handler for the save image button. saves the canvsa to the
    //local file system.
    p.select('#save-image-btn').mouseClicked(function () {
        p.saveCanvas('my-super-picture', 'png');
    });
}
